import countryArr from "@/components/country.json";
import DialogEditAdd from "@/components/addresse/DialogEditAdd";
export default {
  data: function data() {
    return {
      loading: false,
      countryArr: [],
      dialogEditAdd: {
        isShow: false,
        loading: false,
        firstName: '',
        lastName: '',
        address1: '',
        address2: '',
        country: '',
        province: '',
        city: '',
        street: '',
        company: '',
        phone: '',
        postcode: '',
        isDefault: false,
        addressId: ''
      },
      dialogEditAddDefault: '{}',
      items: []
    };
  },
  components: {
    DialogEditAdd: DialogEditAdd
  },
  mounted: function mounted() {
    var _this = this;

    //格式化国家
    Object.keys(countryArr).forEach(function (key) {
      Object.keys(countryArr[key]).forEach(function (key2) {
        _this.countryArr.push({
          label: countryArr[key][key2].nameEN,
          value: countryArr[key][key2].code2
        });
      });
    });
    this.dialogEditAddDefault = JSON.stringify(this.dialogEditAdd);
    this.getAddList();
  },
  methods: {
    delAdd: function delAdd(item) {
      var _this2 = this;

      //删除地址
      this.$confirm('Do you want to delete?', '', {
        confirmButtonClass: " el-button--danger",
        type: "error"
      }).then(function () {
        _this2.loading = true;

        _this2.$apiCall('api.Address.deleteAddress', {
          addressId: item.id
        }, function (r) {
          _this2.loading = false;

          if (r.ErrorCode == "9999") {
            _this2.$elementMessage('delete successfully', "success");

            _this2.getAddList();
          } else {
            _this2.$elementMessage(r.Message, "error");
          }
        });
      }).catch(function () {});
    },
    setAdd: function setAdd() {
      //保存地址成功
      this.getAddList();
      this.dialogEditAdd.isShow = false;
      this.$elementMessage('save successfully', "success");
    },
    openAddAdd: function openAddAdd(item) {
      this.dialogEditAdd = JSON.parse(this.dialogEditAddDefault);

      if (item) {
        //修改
        this.dialogEditAdd.firstName = item.firstName;
        this.dialogEditAdd.lastName = item.lastName;
        this.dialogEditAdd.address1 = item.address1;
        this.dialogEditAdd.address2 = item.address2;
        this.dialogEditAdd.country = item.country;
        this.dialogEditAdd.province = item.province;
        this.dialogEditAdd.city = item.city;
        this.dialogEditAdd.street = item.street;
        this.dialogEditAdd.company = item.company;
        this.dialogEditAdd.phone = item.phone;
        this.dialogEditAdd.postcode = item.postcode;
        this.dialogEditAdd.isDefault = item.isDefault == 1;
        this.dialogEditAdd.addressId = item.id;
      }

      this.dialogEditAdd.isShow = true;
    },
    getAddList: function getAddList() {
      var _this3 = this;

      //获取地址列表
      this.loading = true;
      this.$apiCall(' api.Address.addressList', {}, function (r) {
        _this3.loading = false;

        if (r.ErrorCode == "9999") {
          _this3.items = r.Data.Results;
        } else {
          _this3.$elementMessage(r.Message, "error");
        }
      });
    },
    codeToCountry: function codeToCountry(val) {
      var c = '';
      this.countryArr.some(function (e) {
        if (e.value == val) {
          c = e.label;
          return true;
        }
      });
      return c;
    }
  }
};